.json-inspector {
  padding: 10px 0;
  flex-grow: 1;
}

.json-inspector__key,
.json-inspector__value {
  position: relative;
}

.json-inspector__key {
  color: #666;
}

.json-inspector__value {
  color: #ddd;
}

.json-inspector__selection {
  display: block;
  position: absolute;
  top: 0;
  left: -3px;
  right: 0;
  z-index: 1;
  padding: 0 3px;

  outline: none;
  border: none;
  opacity: 0;
  box-shadow: 3px 3px 4px rgba(0, 0, 0, 0.5), 0 0 0 1px rgba(0, 0, 0, 0.3);
  color: #222;
}

.json-inspector__selection:focus {
  opacity: 1;
}

.json-inspector__show-original::before,
.json-inspector__show-original::after {
  color: #666;
}

.json-inspector__toolbar {
  padding: 0 10px;
}