.workspace-pane {
  display: flex;
  flex-flow: column;
  height: 100%;
  flex-grow: 1;
}

.workspace-pane .header {
  display: flex;
  min-height: 30px;
  height: 30px;
  background-color: #141414;
  border-top: 1px solid #666;
}

.workspace-pane .header .title {
  margin: 0;
  padding: 5px 10px;
  font-size: 16px;
  color: #ccc;
}

.workspace-pane .websocket-form {
  display: flex;
  flex-flow: column;
  padding: 8px;
}

.workspace-pane .scripts {
  padding: 8px;
}

.websocket-form .input-group {
  display: flex;
  justify-content: space-between;
}

.websocket-form .input-group label {
  color: #999;
  display: block;
  margin-bottom: 2px;
  font-size: 14px;
  min-width: 100px;
}

.websocket-form .input-group input {
  width: 100%;
  margin-bottom: 10px;
  align-content: flex-end;
}

.websocket-form .input-group input[type="checkbox"] {
  width: auto;
}

.websocket-form button {
  width: 100px;
  margin: auto;
}