.context-pane {
  display: flex;
  height: 100%;
}

.react-tabs {
  display: flex;
  flex-flow: column;
  flex-grow: 1;
}

.react-tabs__tab-list {
  border-color: #666;
  margin: 0;
  min-height: 33px;
}

.react-tabs__tab:first-child {
  margin-left: 2px;
}

.react-tabs__tab-panel {
  display: none;
  flex-grow: 1;
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
}

.react-tabs__tab-panel--selected {
  display: flex;
  background-color: #252525;
}

.react-tabs__tab--selected {
  border-color: #666;
  border-radius: 0;
  background-color: #141414;
  color: #ccc;
  border-bottom: 1px solid #141414;
}

.ReactTable .rt-table {
  flex-grow: 0;
}

.ReactTable .rt-resizer {
  width: 5px;
  right: -3px;
}