.editor-pane {
  position: relative;
  height: 100%;
  width: 100%;
  display: flex;
  flex-flow: column;
  flex-grow: 1;
}

.editor-header {
  min-height: 40px;
  display: flex;
  justify-content: space-between;
  background-color: #242424;
}

.editor-actions {

}

.file-tabs {
  display: flex;
}

.tab {
  padding: 5px 15px;
  display: flex;
  justify-content: space-between;
  border-top: 2px solid transparent;
}

.tab:hover {
  background-color: #333;
  cursor: pointer;
}

.tab .language-icon {
  width: 22px;
  height: 100%;
  padding-right: 5px;
}

.tab .name {
  padding: 0 5px;
}

.tab .icon {
  visibility: hidden;
  padding-left: 15px;
  user-select: none;
}

.tab:hover .icon {
  visibility: visible;
}

.tab .icon i:hover::before {
  color: lightsalmon;
}

.tab.selected {
  border-top: 2px solid #3ba3cc;
  background-color: #1e1e1e;
}

.tab .name, .tab .icon {
  align-self: center;
}

.monaco-container {
  position: relative;
  display: flex;
  flex-grow: 1;
  height: 100%;
  padding: 5px 0 2px 0;
}

.monaco-greycat {
  flex-grow: 1;
  width: 100%;
  height: 100%;
}

.monaco-greycat .monaco-menu-container .action-item:not(.disabled):hover {
  background-color: #262626;
}

.no-model {
  position: absolute;
  display: flex;
  height: 100%;
  width: 100%;
}

.no-model h2 {
  width: 100%;
  text-align: center;
  align-self: center;
}