.workspace-pane {
  display: flex;
  flex-flow: column;
  height: 100%;
  flex-grow: 1;
}

.workspace-pane .header {
  display: flex;
  min-height: 30px;
  height: 30px;
  background-color: #141414;
  border-top: 1px solid #666;
}

.workspace-pane .header .title {
  margin: 0;
  padding: 5px 10px;
  font-size: 16px;
  color: #ccc;
}

.workspace-pane .websocket-form {
  display: flex;
  flex-flow: column;
  padding: 8px;
}

.workspace-pane .scripts {
  padding: 8px;
}

.websocket-form .input-group {
  display: flex;
  justify-content: space-between;
}

.websocket-form .input-group label {
  color: #999;
  display: block;
  margin-bottom: 2px;
  font-size: 14px;
  min-width: 100px;
}

.websocket-form .input-group input {
  width: 100%;
  margin-bottom: 10px;
  align-content: flex-end;
}

.websocket-form .input-group input[type="checkbox"] {
  width: auto;
}

.websocket-form button {
  width: 100px;
  margin: auto;
}
.editor-pane {
  position: relative;
  height: 100%;
  width: 100%;
  display: flex;
  flex-flow: column;
  flex-grow: 1;
}

.editor-header {
  min-height: 40px;
  display: flex;
  justify-content: space-between;
  background-color: #242424;
}

.editor-actions {

}

.file-tabs {
  display: flex;
}

.tab {
  padding: 5px 15px;
  display: flex;
  justify-content: space-between;
  border-top: 2px solid transparent;
}

.tab:hover {
  background-color: #333;
  cursor: pointer;
}

.tab .language-icon {
  width: 22px;
  height: 100%;
  padding-right: 5px;
}

.tab .name {
  padding: 0 5px;
}

.tab .icon {
  visibility: hidden;
  padding-left: 15px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.tab:hover .icon {
  visibility: visible;
}

.tab .icon i:hover::before {
  color: lightsalmon;
}

.tab.selected {
  border-top: 2px solid #3ba3cc;
  background-color: #1e1e1e;
}

.tab .name, .tab .icon {
  align-self: center;
}

.monaco-container {
  position: relative;
  display: flex;
  flex-grow: 1;
  height: 100%;
  padding: 5px 0 2px 0;
}

.monaco-greycat {
  flex-grow: 1;
  width: 100%;
  height: 100%;
}

.monaco-greycat .monaco-menu-container .action-item:not(.disabled):hover {
  background-color: #262626;
}

.no-model {
  position: absolute;
  display: flex;
  height: 100%;
  width: 100%;
}

.no-model h2 {
  width: 100%;
  text-align: center;
  align-self: center;
}
.ReactTable {
  flex-grow: 1;
  border-top: none !important;
}

.ReactTable .rt-thead.-header {
  background-color: #1c1c1c;
}

.ReactTable .rt-noData {
  display: none !important;
}
.json-inspector {
  padding: 10px 0;
  flex-grow: 1;
}

.json-inspector__key,
.json-inspector__value {
  position: relative;
}

.json-inspector__key {
  color: #666;
}

.json-inspector__value {
  color: #ddd;
}

.json-inspector__selection {
  display: block;
  position: absolute;
  top: 0;
  left: -3px;
  right: 0;
  z-index: 1;
  padding: 0 3px;

  outline: none;
  border: none;
  opacity: 0;
  box-shadow: 3px 3px 4px rgba(0, 0, 0, 0.5), 0 0 0 1px rgba(0, 0, 0, 0.3);
  color: #222;
}

.json-inspector__selection:focus {
  opacity: 1;
}

.json-inspector__show-original::before,
.json-inspector__show-original::after {
  color: #666;
}

.json-inspector__toolbar {
  padding: 0 10px;
}
.context-pane {
  display: flex;
  height: 100%;
}

.react-tabs {
  display: flex;
  flex-flow: column;
  flex-grow: 1;
}

.react-tabs__tab-list {
  border-color: #666;
  margin: 0;
  min-height: 33px;
}

.react-tabs__tab:first-child {
  margin-left: 2px;
}

.react-tabs__tab-panel {
  display: none;
  flex-grow: 1;
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
}

.react-tabs__tab-panel--selected {
  display: flex;
  background-color: #252525;
}

.react-tabs__tab--selected {
  border-color: #666;
  border-radius: 0;
  background-color: #141414;
  color: #ccc;
  border-bottom: 1px solid #141414;
}

.ReactTable .rt-table {
  flex-grow: 0;
}

.ReactTable .rt-resizer {
  width: 5px;
  right: -3px;
}
.console-pane {
  display: flex;
  flex-flow: column;
  height: 100%;
  overflow: hidden;
}

.console-pane .header {
  display: flex;
  padding: 0 10px;
  justify-content: space-between;
  min-height: 30px;
  height: 30px;
  background-color: #141414;
  border-top: 1px solid #666;
}

.console-pane .header .title {
  margin: 0;
  font-size: 16px;
  color: #ccc;
}

.console-pane .header .title .name {
  padding-right: 8px;
}

.console-pane .header .actions .action {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  position: relative;
}

.console-pane .header .actions .action:hover {
  cursor: pointer;
  color: #666;
}

.console-pane .header .actions .action:active {
  cursor: pointer;
  color: #375a7f;
}

.logs-container {
  flex-grow: 1;
  overflow-x: auto;
  overflow-y: auto;
  display: flex;
}

.logs-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  flex-grow: 1;
}

.logs-list .log {
  padding: 4px 10px;
  color: white;
  display: flex;
}

.logs-list .log.debug, .logs-list .log.debug:nth-child(even) {
  background-color: #375a7f;
}
.logs-list .log.debug:hover {
  background-color: #2b4764;
}
.logs-list .log.warn, .logs-list .log.warn:nth-child(even) {
  background-color: #F39C12;
}
.logs-list .log.warn:hover {
  background-color: #d4860b;
}
.logs-list .log.error, .logs-list .log.error:nth-child(even) {
  background-color: #E74C3C;
}
.logs-list .log.error:hover {
  background-color: #e12e1c;
}

.logs-list .log:nth-child(even) {
  background-color: #1d1d1d;
}

.logs-list .log:hover {
  background-color: #292929;
}

.logs-list .log .time {
  margin: 0;
  display: inline-block;
  width: 120px;
}

.logs-list .log .message {
  margin: 0;
}

.logs-list-bottom-marker {
  /* give the marker a width/height in order for scrollIntoView to clip left  */
  height: 2px;
  width: 2px;
}
.muted {
  color: #666;
}

.flex {
  display: flex;
}

.flex-flow-column {
  flex-flow: column;
}
.studio {
  display: flex;
  flex-grow: 1;
  flex-flow: column;
  width: 100%;
  height: 100%;
}

.studio-header {
  display: flex;
  justify-content: space-between;
  padding: 8px 15px;
  background-color: #141414;
}

.header {
  height: 40px;
  display: flex;
  align-items: center;
}

.header-logo {
  width: 40px;
  height: 40px;
}

.header-title {
  display: flex;
  flex-flow: column;
  padding-left: 15px;
}

.header-title h1 {
  margin: 0;
  font-size: 22px;
}

.header-title span {
  font-family: monospace;
  font-size: 12px;
}

.header-actions {
  display: flex;
}

.header-actions .action {
  margin-right: 10px;
  align-self: center;
  width: 35px;
  height: 35px;
  border: 1px solid #2c2c2c;
  border-radius: 2px;
  background-color: #1c1c1c;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  position: relative;
}

.header-actions .action:last-child {
  margin-right: 0;
}

.header-actions .action:hover:not(.disabled) {
  cursor: pointer;
  background-color: #242424;
  border: 1px solid #333;
}

.header-actions .action.disabled::after {
  content: " ";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
}

.header-actions .action.disabled:hover {
  cursor: not-allowed;
}

.header-actions .action:active:not(.disabled) {
  background-color: #2a2a2a;
  border: 1px solid #1c1c1c;
}

.studio-body {
  display: flex;
  flex: 1 1;
}

.main-splitter {
  width: 100%;
}

.studio-body .handle-bar {
  background-color: #333;
  box-shadow: -1px 1px 4px rgba(0,0,0,0.23), 1px 1px 4px rgba(0,0,0,0.23);
}
.studio-body .handle-bar:hover {
  background-color: #444;
}

.editor-pane-container {
  overflow: hidden;
  background-color: #1e1e1e;
}

.studio-layout {
  display: block;
  height: 100%;
  width: 100%;
  overflow-x: hidden;
  overflow-y: auto;
}
