.studio {
  display: flex;
  flex-grow: 1;
  flex-flow: column;
  width: 100%;
  height: 100%;
}

.studio-header {
  display: flex;
  justify-content: space-between;
  padding: 8px 15px;
  background-color: #141414;
}

.header {
  height: 40px;
  display: flex;
  align-items: center;
}

.header-logo {
  width: 40px;
  height: 40px;
}

.header-title {
  display: flex;
  flex-flow: column;
  padding-left: 15px;
}

.header-title h1 {
  margin: 0;
  font-size: 22px;
}

.header-title span {
  font-family: monospace;
  font-size: 12px;
}

.header-actions {
  display: flex;
}

.header-actions .action {
  margin-right: 10px;
  align-self: center;
  width: 35px;
  height: 35px;
  border: 1px solid #2c2c2c;
  border-radius: 2px;
  background-color: #1c1c1c;
  user-select: none;
  position: relative;
}

.header-actions .action:last-child {
  margin-right: 0;
}

.header-actions .action:hover:not(.disabled) {
  cursor: pointer;
  background-color: #242424;
  border: 1px solid #333;
}

.header-actions .action.disabled::after {
  content: " ";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
}

.header-actions .action.disabled:hover {
  cursor: not-allowed;
}

.header-actions .action:active:not(.disabled) {
  background-color: #2a2a2a;
  border: 1px solid #1c1c1c;
}

.studio-body {
  display: flex;
  flex: 1;
}

.main-splitter {
  width: 100%;
}

.studio-body .handle-bar {
  background-color: #333;
  box-shadow: -1px 1px 4px rgba(0,0,0,0.23), 1px 1px 4px rgba(0,0,0,0.23);
}
.studio-body .handle-bar:hover {
  background-color: #444;
}

.editor-pane-container {
  overflow: hidden;
  background-color: #1e1e1e;
}

.studio-layout {
  display: block;
  height: 100%;
  width: 100%;
  overflow-x: hidden;
  overflow-y: auto;
}